  /* background-image: linear-gradient(315deg, #f907fc 0%, #05d6d9 74%); */

}
.c1{
  opacity: 0.9;
  
  
  background-color: white;-webkit-box-shadow: 1px 2px 11px 3px rgba(230,221,230,0.38);
  -moz-box-shadow: 1px 2px 11px 3px rgba(230,221,230,0.38);
  box-shadow: 1px 2px 11px 3px rgba(230,221,230,0.38);
  border-bottom-left-radius: 10px !important;
